import React, { Component } from "react";

import { Row, Col, Card, CardBody, FormGroup, Button, Label, Input, Container, InputGroup, Form, InputGroupAddon } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';

class FormValidations extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Forms", link: "#" },
        { title: "Form Validation", link: "#" },
      ],
      fnm: false,
      lnm: false,
      unm: false,
      city: false,
      stateV: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.changeHandeler.bind(this);
  }

  handleSubmit(e) {
    e.preventDefault();
    var fnm = document.getElementById("validationTooltip01").value;
    var lnm = document.getElementById("validationTooltip02").value;
    var unm = document.getElementById("validationTooltipUsername").value;
    var city = document.getElementById("validationTooltip03").value;
    var stateV = document.getElementById("validationTooltip04").value;
    document.getElementById("tooltipForm").classList.add("was-validated");

    if (fnm === "") { this.setState({ fnm: false }) }
    else { this.setState({ fnm: true }) }

    if (lnm === "") { this.setState({ lnm: false }) }
    else { this.setState({ lnm: true }) }

    if (unm === "") { this.setState({ unm: false }) }
    else { this.setState({ unm: true }) }

    if (city === "") { this.setState({ city: false }) }
    else { this.setState({ city: true }) }

    if (stateV === "") { this.setState({ stateV: false }) }
    else { this.setState({ stateV: true }) }

    var d1 = document.getElementsByName("validate");



    for (var i = 0; i < d1.length; i++) {
      d1[i].style.display = "block";
    }
  }

  //for change tooltip display propery
  changeHandeler(event, eleId) {
    if (event.target.value !== "")
      document.getElementById(eleId).style.display = "none";
    else
      document.getElementById(eleId).style.display = "block";
  }

  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid={true}>



            <Breadcrumbs title="Add User" breadcrumbItems={this.state.breadcrumbItems} />

            <Row>
              <Col lg={12}>
                <Card>
                  <CardBody>
                    <h4 className="card-title">Validation type</h4>
                    <p className="card-title-desc">
                      Parsley is a availity reactstrap validation. It helps you
                      provide your users with feedback on their form submission
                      before sending it to your server.
                  </p>
                 

                    <AvForm>
                       <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label" htmlFor="validationCustom01">First name</Label>
                              <AvField
                                name="firstname"
                                placeholder="First name"
                                type="text"
                                errorMessage="Enter First Name"
                                className="form-control"
                                validate={{ required: { value: true } }}
                                id="validationCustom01"
                              />
                            </div>
                          </Col>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label" htmlFor="validationCustom02">Last name</Label>
                              <AvField
                                name="lastname"
                                placeholder="Last name"
                                type="text"
                                errorMessage="Enter Last name"
                                className="form-control"
                                validate={{ required: { value: true } }}
                                id="validationCustom02"
                              />
                            </div>
                          </Col>
                        </Row>
          
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="email"
                                label="E-Mail"
                                placeholder="Enter Valid Email"
                                type="email"
                                errorMessage="Invalid Email"
                                validate={{
                                  required: { value: true },
                                  email: { value: true }
                                }}
                              />
                            </div>
                          </Col>
                           <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="number"
                                label="Number"
                                placeholder="Enter Only number"
                                type="number"
                                errorMessage="Enter Only Number"
                                validate={{
                                  required: { value: true },
                                  pattern: {
                                    value: "^[0-9]+$",
                                    errorMessage: "Only Numbers"
                                  }
                                }}
                              />
                            </div>
                          </Col>
                         </Row>
                    
                        <Row>
                          <Col md="6">  
                            <div className="mb-3">
                              <Label>Password</Label>
                              <AvField
                                name="password"
                                type="password"
                                placeholder="Password"
                                errorMessage="Enter password"
                                validate={{ required: { value: true } }}
                              />
                              </div>
                            </Col>
                            <Col md="6">
                              <div className="mb-3">
                              <Label>confirm Password</Label>
                              <AvField
                                name="password1"
                                type="password"
                                placeholder="Re-type Password"
                                errorMessage="Enter Re-password"
                                validate={{
                                  required: { value: true },
                                  match: { value: "password" }
                                }}
                              />
                              </div>
                            </Col>
                          </Row>
                      <FormGroup className="mb-0">
                        <div>
                          <Button type="submit" color="primary" className="me-1">
                            Submit
                        </Button>{" "}
                          <Button type="reset" color="secondary">
                            Cancel
                        </Button>
                        </div>
                      </FormGroup>
                    </AvForm>
                  </CardBody>
                </Card>
              </Col>
            </Row>

          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default FormValidations;
